.hello-bar {
    box-sizing: border-box;
    font-size: 90%;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    width: 100%;
}

.hello-bar p {
    margin: 0;
    padding: 0.66rem;
}

.hello-bar.hello-bar--is-fixed {
    position: fixed;
}
